.button {
    border-radius: 3px;
    padding: 5px 24px;
    width: 132px;
    height: 30px;
    border: none;
    font-weight: 600;
    font-size: 14px;
    line-height: 143%;
    color: #fff;
    background: #06c;
}

.disabled {
    background: #004d99;
    opacity: 0.5;
    color: #002e5c;
}

.secondary {
    width: 176px;
    color: #06c;
    background: none;
    border: 1px solid #06c;
    border-radius: 3px;
}

.default {

}

.red {
    background: #cc1439;
    color: #fff;
}

.button:hover {
    width: 176px;
}

.red:hover {
    width: 176px;
}

