.sidebar {
    transform: translateX(-100%);
    width: 320px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    background-color: #FFFFFF;
    display: flex;
    flex-direction: column;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 22px;
}

.sidebar.open {
    transform: translateX(0);
}

.background {
}

.nav {
    display: flex;
    flex-direction: column;
    margin: 23px 0 0 70px;
    gap: 45px;
}

.nav a {
    text-decoration: none;
    color: black;
}

.nav a:hover {
}

.nav a:active {
    color: #3D6B99;
}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;
}
.close {
    align-self: flex-end;
    margin: 16px 24px 0 0;
}

.close img {

}
